<template>
  <div class="root-container" :class="{'list-type-img': switchStyle}" @scroll="handleScroll">
    <skeleton v-if="isSkeletonLoading" :nums="2"></skeleton>
    <div v-else style="height: 100%">
       <div class="search-result-contenter" style='height:100%'>
      <!-- 搜索框 -->
      <div class="search_box" :class="{'hide-scroll':!showSearchBar}">
        <van-sticky>
          <van-nav-bar left-arrow @click-left="goBack">
