            <slot name="title" slot="title">
              <div style='display:flex;flex-direction:row;align-items:center;height:0.92rem;'>
                <div class="search-input" @click.stop="showSearchComponent">
                  <img class="search-input-img" :src="$globalIcon.commonSearchIcon" alt="">
                  <div class="search-hotwords" :class="{holderfont:!keyword}">{{keyword || placeholder}}</div>
                  <van-icon size='0.32rem' class="search-clear" name="clear" @click="clearFn" />
                </div>
                  <!-- <van-field
                    readonly
                    ref='search'
                    left-icon="search"
                    v-model="keyword"
                    show-action
                    shape="round"
                    maxlength="20"
                    placeholder="请输入搜索内容"
                    class='search-filed'
                    :clearable="true"
                  /> -->
                <!-- <div  @click='showSearchComponent'>123123</div> -->
                <div slot="action" class="layoutImg" @click="layout">
                  <!-- <img v-if="switchStyle" alt :src="`${$cdnImageUrl}/search/layout_new.png` | imageToHttps" />
                  <img v-else alt :src="`${$cdnImageUrl}/search/layoutSingle_new.png` | imageToHttps" /> -->
                  <img v-if="switchStyle" alt :src="`${$cdnImageUrl}/search/home_navigation_search_goodslist_gongge@3x.png` | imageToHttps" />
                  <img v-else alt :src="`${$cdnImageUrl}/search/home_navigation_search_goodslist_liebiao@3x.png` | imageToHttps" />
