              <li :class="{active:searchType === 'shop'}" @click="fnChangeSearchType('shop')">店铺</li>
            </ul>
          </div> -->
          <div class="filter-wrap" v-show="(keyword || placeholder!=='请输入搜索内容') && !keySearchShowFlag && searchFlag && searchType === 'goods'">
            <div class="left">
              <ul>
                <li :class="{ active: qs === 'isHotDesc' || qs === 'comments'}" @click="fnChangeHotModuleShowFlag">
                  <div class="text">{{qs === 'isHotDesc'?'综合':qs === 'comments'?'评论':'综合'}}</div>
                  <div class="icon combined_icon">
                    <img alt :src="`${$cdnImageUrl}/search/zonghe_blue.png` | imageToHttps" v-if="qs === 'isHotDesc' || qs === 'comments'" />
                    <img alt :src="`${$cdnImageUrl}/search/zonghe_gray.png` | imageToHttps" v-if="qs !== 'isHotDesc' && qs !== 'comments'" />
                  </div>
                </li>
                <li :class="{ active: qs === 'saleDesc' }" @click="doFilter('sale')">
                  <div class="text">销量</div>
                </li>
                <li :class="{ active: qs === 'commission' || qs === 'commissionDesc' }" @click="doFilter('commission')" v-if="$toggleSwitch.commissionDisplay">
                  <div class="text">佣金</div>
                  <div class="icon">
                    <img alt :src="`${$cdnImageUrl}/search/triangle_up_light.png` | imageToHttps" v-if="qs !== 'commission'" />
                    <img alt :src="`${$cdnImageUrl}/search/triangle_up_dark.png` | imageToHttps" v-if="qs === 'commission'" />
                    <img alt :src="`${$cdnImageUrl}/search/triangle_down_light.png` | imageToHttps" v-if="qs !== 'commissionDesc'" />
                    <img alt :src="`${$cdnImageUrl}/search/triangle_down_dark.png` | imageToHttps" v-if="qs === 'commissionDesc'" />
                  </div>
                </li>
                <li :class="{ active: qs === 'price' || qs === 'priceDesc' }" @click="doFilter('price')">
                  <div class="text">价格</div>
                  <div class="icon price-icon">
                    <img alt :src="`${$cdnImageUrl}/search/price_up_gray.png` | imageToHttps" v-if="qs !== 'price'" />
                    <img alt :src="`${$cdnImageUrl}/search/price_up_blue.png` | imageToHttps" v-if="qs === 'price'" />
                    <img alt :src="`${$cdnImageUrl}/search/price_down_gray.png` | imageToHttps" v-if="qs !== 'priceDesc'" />
                    <img alt :src="`${$cdnImageUrl}/search/price_down_blue.png` | imageToHttps" v-if="qs === 'priceDesc'" />
                  </div>
                </li>
                <li :class="{'filter':true,'filter-actived':fnChangeFlag}" @click="fnShowGoodsFilter">
                  <!-- <div style='height:0.4rem;width:1px;background-color:#eee;position: relative;right:0.6rem'></div> -->
                  <div class="text">筛选</div>
                  <!-- <img alt :src="`${$cdnImageUrl}/shop/icon-shaixuan.png` | imageToHttps" /> -->
                  <span class="filter-icon"></span>
                </li>
              </ul>
            </div>
          </div>
          <div class="activity-tabs" v-if="activityTabs.length">
            <div v-for="item in activityTabs" :key="item.id" :class="{'active': item.id == curActiveTab}" @click="changeActivityTab(item)">优先展示{{item.tagName}}商品</div>
          </div>
        </van-sticky>
      </div>
      <!-- 参照元素 -->
      <div class="target" ref="toTop"></div>
      <!-- <lazy-search style='z-index: 1000;height:100px'/> -->
      <lazy-search  from='goodsSearch' hiddenId="pop-up-hidden" @go-back="hideSearchComponent" :storeId='storeId' @search='searchEvent' v-show='searchComponentShow' :show='searchComponentShow' :words='clear?"":keyword'/>
      <div :class="{'search_content':true,'scroll-hidden':scrollHidden}">
        <!-- 点击综合弹层 -->
        <div class="popup" :style='{top:showSearchBar?"1.66rem":"0.86rem"}' v-show="hotModuleShowFlag" @touchmove.prevent @click="fnChangeHotModuleShowFlag">
          <ul @click.stop>
            <li :class="{active: qs === 'isHotDesc'}" @click="doFilter('hot')">
              <div class="left">综合排序</div>
              <div class="right">
                  <!-- <img :src="`${$cdnImageUrl}/search/true.png` | imageToHttps" v-if="qs === 'isHotDesc'" />
                  <img :src="`${$cdnImageUrl}/search/grayTrue.png` | imageToHttps" v-if="qs !== 'isHotDesc'" /> -->
              </div>
            </li>
            <li :class="{active: qs === 'comments'}" @click="doFilter('comment')">
              <div class="left">评论数从高到低</div>
              <div class="right">
                  <!-- <img :src="`${$cdnImageUrl}/search/true.png` | imageToHttps" v-if="qs === 'comments'" />
                  <img :src="`${$cdnImageUrl}/search/grayTrue.png` | imageToHttps" v-if="qs !== 'comments'" /> -->
              </div>
            </li>
          </ul>
        </div>
        <!-- 搜索商品、店铺无数据 -->
        <div :class="{'product-nodata':true,'img-top-30':searchType === 'shop','has-storeinfo': storeInfo && storeInfo.id}" v-show="(keyword || placeholder!=='请输入搜索内容') && ((searchType === 'goods' && !productList.length) || (searchType === 'shop' && !storeList.length)) && searchFlag && !requestFlag">
          <lazy-store-info v-if='storeInfo && storeInfo.id' :storeInfo="storeInfo"/>
          <img alt :src="`${$cdnImageUrl}/search/noWord.png`" />
          <p>换个词试一下</p>
          <want-btn :type="'blue'" v-if="isUplus"></want-btn>
        </div>
        <!-- 关键词搜索 -->
        <!-- <van-popup
          overlay-class="zindex9995"
          v-model="keySearchShowFlag"
          position="top"
          :duration="0"
          :style="{ height: '100%' }"
        >
          <key-search v-show="keySearchShowFlag" :keyword="keyword" :keywordsList="keywordsList" @doSearch="doSearch" />
        </van-popup> -->
        <!-- <key-search v-show="keySearchShowFlag" :keyword="keyword" :keywordsList="keywordsList" @doSearch="doSearch" /> -->

        <!-- <key-search v-show="keySearchShowFlag" :keyword="keyword" :keywordsList="keywordsList" @doSearch="doSearch" /> -->
        <!-- 商品列表 -->
        <!-- <product-list v-show="(keyword || placeholder!=='请输入搜索内容') && productList.length" :productList="productList" :finished="finished" :vantLoading="vantLoading" :vantIsLoading="vantIsLoading" @onLoad="onLoad" @onRefresh="onRefresh" /> -->
        <van-pull-refresh class="search-warp" :class="{'hasTab': activityTabs.length}" v-show="(keyword || placeholder!=='请输入搜索内容') && productList.length && searchType === 'goods'" v-model="isLoading" @refresh="onRefresh">
          <template #pulling><refreshLoading text="下拉刷新"/></template>
          <template #loosing><refreshLoading text="下拉刷新"/></template>
          <template #loading><refreshLoading text="加载中"/></template>
          <template #success><refreshLoading text="加载完成"/></template>
          <div class="list-header">
            <lazy-store-info v-if='storeInfo && storeInfo.id' :storeInfo="storeInfo"/>
          </div>
          <van-list :finished="finished" :immediate-check="false" :offset="10" @load="onLoad" finished-text="没有更多啦~" v-model="loading">
            <lazy-public-product-list id='pop-up-hidden' :skulive="productSkus"  :productList="productList" :switchStyle='switchStyle' :visitStore="storeId" :taskId="taskId" :stageId="stageId"/>
            <template slot="finished">
              <want-btn :type="switchStyle?'white':'gray'"></want-btn>
            </template>
          </van-list>
        </van-pull-refresh>
        <!-- 店铺列表 -->
        <!-- <van-pull-refresh v-show="(keyword || placeholder!=='请输入搜索内容') && storeList.length && searchType === 'shop'" v-model="isLoading" @refresh="onRefresh">
          <template #pulling><refreshLoading text="下拉刷新"/></template>
          <template #loosing><refreshLoading text="下拉刷新"/></template>
          <template #loading><refreshLoading text="加载中"/></template>
          <template #success><refreshLoading text="加载完成"/></template>
          <van-list :finished="finished" :immediate-check="false" :offset="10" @load="onLoad" finished-text="没有更多啦~" v-model="loading">
            <lazy-shop-list :storeList="storeList"/>
          </van-list>
        </van-pull-refresh>-->
      </div>
      <!-- 筛选框 -->
      <van-popup overlay-class="zindex9998" v-if='filterLoad' v-model="filterFlag" @close="onGoodsFilterHide" :style="{ width: '7rem', height: '100vh' }" position="right">
        <lazy-goods-filter
          ref="filter"
          :stock="stock"
          :price="price"
          :brandList="brandList"
          :lstAttributes="lstAttributes"
          :location="location"
          @fnConfirm="fnConfirm"
          @fnFold="fnFold"
          @fnReset="fnReset"
          @fnSelect="fnSelect"
          @fnShowGoodsFilter="fnShowGoodsFilter"
          @changeAddr="changeAddr"
        />
      </van-popup>
        <!-- 返回顶部 -->
      <top-feedBack :isShow="showToTopFlag && !!productList.length" @hiddenFeedBack="hiddenFeedBack" @toTop="fnScrollToTop"></top-feedBack>
      <van-popup overlay-class="zindex9998" v-model="addrShow" :style="{ width: '7rem', height: '100vh' }" position="right">
          <!-- 地址选择列表 -->
          <lazy-addr-select  :addrList="addrData" v-if="addrShow" @onHide="onHideAddr" @handleLocation="handleLocation"  @otherLocation="otherLocationOption" />
      </van-popup>
      <van-popup overlay-class="zindex9998" v-model="locationshow" :style="{ width: '7rem', height: '100vh' }" position="right">
          <!-- 选择地理位置 -->
          <lazy-change-geographic-location v-if="locationshow" :isShowBack="true" @onHide="onHideLocation" @closeAndBack="closeAndBack" @handleLocation="handleLocation" />
      </van-popup>
    </div>
    </div>
  </div>
</template>

<script>
// import utils from '@/utils/utils'
import refreshLoading from '@/components/refreshLoading'
// import topFeedBack from '@/components/top-feedBack/index'
import mixins from '@/components/top-feedBack/mixins_inpage_scroll.js'
import wantBtn from '@/components/want-btn/index'
import { wdCommonSearchNew, getSkusLiveInfo, searchStore, getSelectBoxTagList, associatedSearchStore } from '@/http/service/search'
import { getPriceByProductList, wdMarketFiltrate } from '@/http/service/shop'
import personalApi from '@/http/service/index';
import skeleton from '@/components/skeleton/search'
const lazyStoreInfo = () => import(/* webpackChunkName: "store-info" */'./components/store-info')
const lazyPublicProductList = () => import(/* webpackChunkName: "productList" */'@/components/publicProductList/index')
const lazyAddrSelect = () => import(/* webpackChunkName: "addrSelect" */'./components/addr-select')
const lazyChangeGeographicLocation = () => import(/* webpackChunkName: "geographic" */'./components/change-geographic-location')
const lazySearch = () => import(/* webpackChunkName: "searchComponent" */'@/components/search/index.vue')
const lazyGoodsFilter = () => import(/* webpackChunkName: "goodsFilter" */'@/views/shop/shop/components/goods-filter')
// const lazyShopList = () => import(/* webpackChunkName: "shopList" */'./components/shop-list')
let initFlag = false
let preScrollTop = 0;
export default {
  name: 'goodsSearch',
  mixins: [mixins],
  components: {
    skeleton,
    refreshLoading,
    lazySearch,
    // productList,
    lazyGoodsFilter,
    // lazyShopList,
    lazyChangeGeographicLocation,
    lazyAddrSelect,
    lazyPublicProductList,
    lazyStoreInfo,
    // topFeedBack,
    wantBtn
  },
  data() {
    return {
      isSkeletonLoading: true,
      searchComponentLoad: false,
      searchComponentShow: false,
      searchComponentShowDelay: false,

      activityTabs: [], // 活动tab
      curActiveTab: '', // 当前活动tab

      filterLoad: false,
      placeholder: '', // 输入框的默认值
      currentIndex: 0, // 当前holder的索引值
      searchType: 'goods', // 搜索 商品、店铺切换
      keySearchShowFlag: false, // 关键词展示框
      searchFlag: false, // 是否搜索过
      hotModuleShowFlag: false, // 综合筛选弹层
      keyword: '', // 绑定的输入框的值
      qs: 'isHotDesc', // 默认 综合人气优先
      filterData: 'hasStock@all@0',
      pageIndex: 1,
      pageSize: 10,
      productList: [], // 商品列表
      finished: false,
      loading: false,
      isLoading: false,
      filterFlag: false,
      stock: 'hasStock',
      price: '',
      brandList: [],
      lstAttributes: [],
      // showToTopFlag: false,
      requestFlag: false,
      storeList: [], // 店铺列表
      fromType: '1', // 区分从店铺进来搜索传2，首页搜索传1
      visitStore: '', // 访问店铺id
      switchStyle: false,
      newKeyWord: false,
      addrShow: false,
      locationshow: false,
      addrData: [], // 登录人地址列表
      fnChangeFlag: false, // 筛选项是否被改变标记
      storeId: '',
      oldLocationStr: '', // 暂存上次搜索的地址全局变量
      oldStoreId: '', // 暂存上次搜索的店铺id
      scrollPosition: 0,
      scrollHidden: false,

      productSkus: {}, // 获取直播商品skus
      clear: false, // 点击弹窗时是否清空keyword
      showSearchBar: true,

      storeInfo: undefined,
      isUplus: false, // 验证是否智家app内
      scrollTarget: '.root-container', // 页面滚动元素
      stageId: '',
      taskId: '',
    }
  },
  computed: {
    location() {
      return this.$store.getters['getlocationInfo']
    },
    memberId() {
      return sessionStorage.getItem('shareUserId') || '';
    }
  },
  created() {
    this.storeId = this.$route.query.visitStore
    this.isUplus = this.$util.isUplus()
    this.taskId = this.$route.query.taskId || ''
    this.stageId = this.$route.query.stageId || ''
    if (this.storeId) {
      this.fromType = 2
    } else {
      this.fromType = 1
    }
    this.showToTopFlag = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop > 150
    console.log(this.showToTopFlag)
    sessionStorage.setItem('search-keyword', '')
    // 若已登录加载地址列表
    if (this.$store.getters.getLoginStatus) {
      this.getAddress()
    }

    // 获取活动标签信息
    this.getSelectBoxTagList()

    // 暂存地址与店铺id
    //  provinceId: this.location.provinceId,
    //     cityId: this.location.cityId,
    //     districtId: this.location.districtId,
    //     streetId: this.location.townshipId,
    this.oldLocationStr = this.location.provinceId + '|' + this.location.cityId + '|' + this.location.districtId + '|' + this.location.townshipId
    this.oldStoreId = this.storeId
  },
  mounted() {
    if (this.$route.query.productCateId) {
      // 从分类进入
      this.initParam()
      // this.placeholder = '请输入搜索内容' // 在分类中展示placeholder
      this.showToTopFlag = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop > 150
      this.productCateId = this.$route.query.productCateId
      this.filterData = this.$route.query.filterData || 'hasStock@all@0'
      if (this.filterData) {
        // 处理 filterData
        this.stock = this.filterData.split('@')[0]
        this.brandListIds = this.filterData.split('@')[1].split(';')
        this.lstAttributesIds = this.filterData.split('@')[3] ? this.filterData.split('@')[3].split(';') : ''
      }
      this.wdCommonSearchNew().then(i => {
        // 埋点 DSG-22413
        this.$util.gioTrackWithVariable('MB15630', {
          search_words: this.keyword ? this.keyword + '' : 'null',
          category_id: this.productCateId ? this.productCateId + '' : 'null',
          storeId: this.storeId ? this.storeId + '' : 'null'
        })
      }).catch(e => { console.log(e) });
      return
    }

    this.storeId = this.$route.query.visitStore
    if (this.storeId) {
      this.fromType = 2
    } else {
      this.fromType = 1
    }
    let keyword = this.$route.query.keyword;
    this.newKeyWord = !keyword
    if (keyword && keyword !== sessionStorage.getItem('search-keyword')) {
      sessionStorage.setItem('search-keyword', keyword)
      // app跳搜索 带关键字
      this.pageIndex = 1
      this.keyword = this.$route.query.keyword
      this.doSearch()
      // this.initParam()
    } else if (initFlag && keyword !== sessionStorage.getItem('search-keyword')) {
      this.initParam()
      initFlag = false
    }

    // 店铺发生变化  解决： 店铺——》搜索1-》详情-》切换位置-》跳搜索2（不刷新） -》返回到搜索1（不刷新） 场景问题
    if (this.storeId !== this.oldStoreId && this.storeId && this.oldStoreId) {
      console.log('店铺发生变化')
      this.oldStoreId = this.storeId
      this.initParam()
      this.keyword = this.$route.query.keyword // 解决上述跳回后丢失关键字问题
    }
    // 地址发生变化
    let newLocationStr = this.location.provinceId + '|' + this.location.cityId + '|' + this.location.districtId + '|' + this.location.townshipId
    if (newLocationStr !== this.oldLocationStr) {
      console.log('地址发生变化')

      this.oldLocationStr = newLocationStr
      if (this.keyword) {
        this.requestFlag = false
        // this.keyword = sessionStorage.getItem('search-keyword')
        // console.log('keyword：', this.keyword)
        this.doSearch()
      }
    }
    // 如果没有关键字 则直接弹出搜索框
    if (!this.keyword) {
      let timer = setTimeout(() => {
        clearTimeout(timer);
        lazySearch().then(() => {
          this.isSkeletonLoading = false
          this.showSearchComponent();
        })
      }, 100)
    }
  },
  beforeRouteEnter(to, from, next) {
    // if (utils.isSgRn()) {
    //   window.postMessage(
    //     JSON.stringify({
    //       type: 'goSearch',
    //       data: {
    //         storeId: to.query.visitStore || '',
    //       }
    //     })
    //   )
    //   next(false)
    // } else {
    if (from.name === 'home') {
      initFlag = true
      to.meta.scrollTop = 0;
    }
    next();
    // }
  },
  beforeRouteLeave (to, from, next) {
    this.scrollPosition = document.querySelector('.root-container').scrollTop
    console.log(from, '=======from========')
    const pres = history.state ? history.state : {};
    console.log(pres);
    let state = Object.assign(pres, { title: '', url: window.location.href });
    const query = this.$util.parseParamsToQuery({ ...from.query, keyword: this.keyword })
    const prePath = process.env.VITE_ENV ? '' : '/sgmobile';
    const url = prePath + from.path + query;
    history.replaceState(state, '', url);
    next();
  },
  activated() {
    // 跳转到指定位置
    this.$nextTick(() => {
      const top = this.scrollPosition
      document.querySelector('.root-container').scrollTo(0, top || 0);
    });
  },
  deactivated() {
    // if (this.$route.meta.isUseCache) {
    //   this.productList = [];
    //   this.keyword = '';
    //   sessionStorage.removeItem('search-keyword')
    // }
  },
  methods: {
    /**
     * 获取活动标签
     */
    getSelectBoxTagList() {
      getSelectBoxTagList().then(res => {
        let result = res.data;
        console.log(result, 'result getSelectBoxTagList')
        this.activityTabs = result || []
      })
    },
    /**
     * 活动优先展示切换
     */
    changeActivityTab(item) {
      if (this.curActiveTab === item.id) {
        this.curActiveTab = '';
      } else {
        this.curActiveTab = item.id;
      }
      console.log('changeActivityTab')
      this.doSearch()
    },

    showSearchComponent() {
      // if (!this.searchComponentLoad) {
      //   lazySearch().then(() => {
      //     this.searchComponentLoad = true;
      //     this.searchComponentShow = true;
      //     this.$nextTick(() => {
      //       this.searchComponentShowDelay = true;
      //     })
      //   })
      //   return;
      // }
      this.searchComponentShow = true;
      // this.$nextTick(() => {
      //   this.searchComponentShowDelay = true;
      // })
    },
    hideSearchComponent() {
      this.clear = false;
      if (!this.keyword) {
        this.goBack();
      }
      this.searchComponentShow = false;
    },
    handleScroll(e) {
      this.showToTopFlag = e.target.scrollTop > 200
      if (e.target.scrollTop - preScrollTop >= 0 && e.target.scrollTop > 100) {
        this.showSearchBar = false;
      } else {
        this.showSearchBar = true;
      }
      preScrollTop = e.target.scrollTop;
    },
    changeAddr() {
      this.addrShow = true
    },
    onHideAddr() {
      this.addrShow = false
    },
    handleLocation() {
      this.locationshow = false
      this.addrShow = false
    },
    otherLocationOption() {
      this.locationshow = true
    },
    onHideLocation() {
      this.locationshow = false
    },
    closeAndBack() {
      this.locationshow = false
    },
    /**
     * @description 修改布局格式
     */
    layout() {
      this.switchStyle = !this.switchStyle
    },
    /**
     * @description 搜索 商品、店铺切换
     */
    fnChangeSearchType(type) {
      // 重复点击
      if (this.searchType === type) {
        return
      }
      // 请求中
      if (this.requestFlag) {
        return
      }
      this.searchType = type
      // 没有搜索过 不搜索
      if (!this.searchFlag) {
        return
      }
      // 搜索
      this.doSearch()
    },
    /**
     * @description 接受搜索事件
     */
    searchEvent(keyword) {
      this.clear = false;
      this.searchComponentShow = false;
      this.productCateId = '' // 在分类中搜索时 清空分类ID再搜索
      if (keyword && keyword.alreayRoute) {
        this.searchComponentShow = true;
      }
      if (typeof keyword === 'string') {
        console.log('++++++++keyword+++++++++++')
        this.curActiveTab = ''
        this.doSearch(keyword);
      }
    },
    /**
     * @description 搜索
     */
    doSearch(keyword) {
      if (keyword) {
        this.keyword = keyword;
      } else {
        keyword = this.keyword
      }
      sessionStorage.setItem('search-keyword', keyword)
      // 添加历史记录
      this.$store.commit('history/addHistoryItem', keyword)
      // 重置搜索数据
      this.stock = 'hasStock'
      this.price = ''
      this.qs = 'isHotDesc'
      this.filterData = 'hasStock@all@0'
      this.brandList = []
      this.lstAttributes = []
      // this.productList = []
      this.pageIndex = 1
      // this.finished = false
      this.wdCommonSearchNew().then(_ => {
        // 埋点 DSG-22413
        this.$util.gioTrackWithVariable('MB15630', {
          search_words: this.keyword ? this.keyword + '' : 'null',
          category_id: this.productCateId ? this.productCateId + '' : 'null',
          storeId: this.storeId ? this.storeId + '' : 'null'
        })
        this.$nextTick(() => {
          this.$el.querySelector('.target').scrollIntoView({
            block: 'start',
          }) // 回到顶部
        })
      }).catch(e => {
        console.log(e)
      })
      // utils.replaceState({ keyword: this.keyword });
    },
    doFilter(type) {
      if (type === 'hot') {
        this.hotModuleShowFlag = false
        if (this.qs === 'isHotDesc') {
          return
        }
        this.qs = 'isHotDesc'
      } else if (type === 'sale') {
        this.$util.gioTrackWithVariable('MB15632')
        this.hotModuleShowFlag = false // 关闭人气优先弹层
        if (this.qs === 'saleDesc') {
          return
        }
        this.qs = 'saleDesc'
      } else if (type === 'commission') {
        this.hotModuleShowFlag = false // 关闭人气优先弹层
        if (this.qs === 'commission') {
          this.qs = 'commissionDesc'
        } else {
          this.qs = 'commission'
        }
      } else if (type === 'price') {
        this.$util.gioTrackWithVariable('MB15633', {
          sort: this.qs === 'price' ? '降序' : '升序'
        })
        this.hotModuleShowFlag = false // 关闭人气优先弹层
        if (this.qs === 'price') {
          this.qs = 'priceDesc'
        } else {
          this.qs = 'price'
        }
      } else if (type === 'comment') {
        this.hotModuleShowFlag = false // 关闭人气优先弹层
        if (this.qs === 'comments') {
          return
        }
        this.qs = 'comments'
      }
      // this.productList = []
      this.pageIndex = 1
      // this.finished = false
      this.wdCommonSearchNew().catch(e => { console.log(e) })
      this.$el.querySelector('.target').scrollIntoView({
        block: 'start',
      }) // 回到顶部
    },
    /**
     * @description 点击商品筛选
     */
    fnShowGoodsFilter() {
      this.scrollHidden = true
      if (!this.filterFlag) {
        // 解决第一次呈现没有动画的问题
        lazyGoodsFilter().then(() => {
          this.filterLoad = true;
          this.$nextTick(_ => {
            this.filterFlag = !this.filterFlag
            this.fnHasChanged()
          })
        })
      } else {
        this.filterFlag = !this.filterFlag
        this.fnHasChanged()
      }
      this.hotModuleShowFlag = false
    },
    onGoodsFilterHide() {
      this.scrollHidden = false
    },
    /**
     * @description 商品筛选折叠
     */
    fnFold(type) {
      this.lstAttributes.forEach(item => {
        if (item.id === type) {
          item.foldFlag = !item.foldFlag
        }
      })
      this.lstAttributes.splice(0, 1, this.lstAttributes[0]) // 强制刷新页面
    },
    /**
     * @description 商品筛选
     */
    fnSelect(data) {
      let _selectAll
      if (data.type === 'brand') {
        if (data.index === 0) {
          this.brandList.forEach(item => {
            item.selected = false
          })
          this.brandList[0].selected = true
        } else {
          this.brandList[0].selected = false
          this.brandList[data.index].selected = !this.brandList[data.index].selected
          const _brandList = [...this.brandList]
          _brandList.splice(0, 1)
          _selectAll = _brandList.some(item => item.selected === false)
          if (!_selectAll) {
            this.brandList.forEach(item => {
              item.selected = false
            })
            this.brandList[0].selected = true
          }
        }
        _selectAll = this.brandList.some(item => item.selected === true)
        if (!_selectAll) {
          this.brandList[0].selected = true
        }
        this.brandList.splice(data.index, 1, this.brandList[data.index]) // 强制页面刷新
      } else if (data.type === 'other') {
        if (data.subIndex === 0) {
          this.lstAttributes[data.index].lstAttributeOptions.forEach(item => {
            item.selected = false
          })
          this.lstAttributes[data.index].lstAttributeOptions[0].selected = true
        } else {
          this.lstAttributes[data.index].lstAttributeOptions[0].selected = false
          this.lstAttributes[data.index].lstAttributeOptions[data.subIndex].selected = !this.lstAttributes[data.index].lstAttributeOptions[data.subIndex].selected
          const _lstAttributeOptions = [...this.lstAttributes[data.index].lstAttributeOptions]
          _lstAttributeOptions.splice(0, 1)
          _selectAll = _lstAttributeOptions.some(item => item.selected === false)
          if (!_selectAll) {
            this.lstAttributes[data.index].lstAttributeOptions.forEach(item => {
              item.selected = false
            })
            this.lstAttributes[data.index].lstAttributeOptions[0].selected = true
          }
        }
        _selectAll = this.lstAttributes[data.index].lstAttributeOptions.some(item => item.selected === true)
        if (!_selectAll) {
          this.lstAttributes[data.index].lstAttributeOptions[0].selected = true
        }
        this.lstAttributes.splice(data.index, 1, this.lstAttributes[data.index]) // 强制页面刷新
      } else if (data.type === 'stock') {
        this.stock = data.index
      }
    },
    /**
     * 清空搜索输入
     */
    clearFn() {
      this.clear = true;
      this.curActiveTab = ''
    },
    /**
     * @description 商品筛选 重置
     */
    fnReset() {
      // 重置库存
      this.stock = 'hasStock'
      // 重置价钱
      this.price = ''
      // 重置品牌
      if (this.brandList.length > 0) {
        this.brandList.forEach(item => {
          item.selected = false
        })
        this.brandList[0].selected = true
        this.brandList.splice(0, 1, this.brandList[0]) // 强制页面刷新
      }
      // 重置其他
      if (this.lstAttributes.length > 0) {
        this.lstAttributes.forEach((item, index) => {
          item.lstAttributeOptions.forEach((subItem, subIndex) => {
            subItem.selected = false
            if (subIndex === 0) {
              subItem.selected = true
            }
          })
        })
        this.lstAttributes.splice(0, 1, this.lstAttributes[0]) // 强制页面刷新
      }
      this.fnHasChanged()
    },
    /**
     * @description 筛选项是否有变动
     */
    fnHasChanged() {
      this.fnChangeFlag = false;
      this.$nextTick(() => {
      // 库存
        if (this.stock !== 'hasStock') this.fnChangeFlag = true;
        // 价钱
        if (this.price !== '') this.fnChangeFlag = true;
        // 筛选组件内最高价与最低价
        if (this.$refs.filter && (this.$refs.filter.lowestPrice || this.$refs.filter.highestPrice)) this.fnChangeFlag = true;
        // 品牌
        if (this.brandList.length > 0) {
          this.brandList.forEach((item, index) => {
            if (item.selected && index !== 0) this.fnChangeFlag = true;
          })
        }
        // 其他
        if (this.lstAttributes.length > 0) {
          this.lstAttributes.forEach((item, index) => {
            item.lstAttributeOptions.forEach((subItem, subIndex) => {
              if (subItem.selected && subIndex !== 0) this.fnChangeFlag = true;
            })
          })
        }
      })
    },
    /**
     * @description 商品筛选 确定
     */
    fnConfirm(data) {
      this.price = data.price
      let gioArr = [] // gio参数数组
      gioArr.push(this.location.region)
      gioArr.push(this.stock)
      gioArr.push(this.price)
      // 格式 库存@品牌@价格@类目特殊
      let _brand = ''
      if (this.brandList.length > 0) {
        this.brandList.forEach((item, index) => {
          if (item.selected) {
            _brand = `${_brand}${item.id};`
            gioArr.push(item.brandName)
          }
        })
        _brand = _brand.slice(0, _brand.length - 1)
      }
      let _other = []
      if (this.lstAttributes.length > 0) {
        this.lstAttributes.forEach((item, index) => {
          let _otherArr = []
          item.lstAttributeOptions.forEach(subItem => {
            if (subItem.selected) {
              _otherArr.push(subItem.id)
              gioArr.push(subItem.optionValue)
            }
          })
          _other.push(_otherArr.join(';'))
        })
      }
      this.$util.gioTrackWithVariable('MB15634', {
        filter: gioArr.join(',')
      })
      _other = _other.join(',')
      this.filterData = `${this.stock}@${_brand}@${data.price}@${_other}`
      console.log('this.filterData:', this.filterData)
      // this.productList = []
      this.pageIndex = 1
      // this.finished = false
      this.wdCommonSearchNew().catch(e => { console.log(e) })
      this.$el.querySelector('.target').scrollIntoView({
        block: 'start',
      }) // 回到顶部
      this.fnHasChanged()
    },

    /**
     * @description 推荐店铺
    */
    associatedSearchStore(keyword, filterData, cateId) {
      let brandId;
      if (filterData) {
        if (filterData.indexOf('@') !== -1) {
          brandId = filterData.split('@')[1];
        }
      }
      if (brandId === 'all') {
        brandId = undefined;
      }
      associatedSearchStore({
        keyName: keyword,
        brandId: brandId,
        productCateId: cateId || undefined,
        visitStore: this.storeId || '',
        shareId: this.$store.getters.getShareId || '' // 分享人id
      }).then((res) => {
        if (res.success) {
          this.storeInfo = res.data;
        } else {
          console.log(res.message);
        }
      }).catch(e => {
        console.log(e);
      })
    },
    /**
     * @description 关键词搜索 商品列表
     */
    wdCommonSearchNew() {
      if (this.requestFlag) {
        return Promise.reject(new Error('this.requestFlag is true'))
      }
      // 新增推荐店铺
      this.associatedSearchStore(this.keyword || this.placeholder || '', this.filterData, this.productCateId);
      this.requestFlag = true
      return new Promise((resolve, reject) => {
        wdCommonSearchNew({
          provinceId: this.location.provinceId,
          cityId: this.location.cityId,
          districtId: this.location.districtId,
          streetId: this.location.townshipId,
          fromType: this.fromType,
          visitStore: this.storeId, // 访问店铺id
          memberId: this.memberId, // 分享人id
          keyword: this.keyword || this.placeholder || '',
          qs: this.qs,
          filterData: this.filterData,
          pageIndex: this.pageIndex,
          pageSize: this.pageSize,
          actTagId: this.curActiveTab || '',
          productCateId: this.productCateId,
          // searchType: 0,
        }).then(async res => {
          this.isSkeletonLoading = false;
          if (res.success) {
            this.finished = res.data.productList.length !== this.pageSize
            let skus = [] // 直播skus处理
            if (res.data.productList.length) {
              const _productList = await this.getPriceByProductList(res.data.traceId)
              res.data.productList.forEach(item => {
                skus.push(item.productId);
                // this.productParamsids = this.productParamsids.concat()
                _productList.forEach(_item => {
                  if (item.sku === _item.sku) {
                    item.finalPrice = _item.lowestFlashPrice || _item.finalPrice
                    item.isLowestFlash = !!_item.lowestFlashPrice
                    item.skku = _item.skku
                    item.commission = _item.commission
                    item.productAct = _item.productAct
                    item.productActNew = _item.productActNew
                    item.productActTerm = _item.productActTerm
                    item.priceType = _item.priceType
                    item.defaultPrice = _item.defaultPrice
                    item.promotion = _item.promotion
                    item.plusPrice = _item.plusPrice
                    item.minCommission = _item.minCommission
                    item.maxCommission = _item.maxCommission
                    item.clustersNum = _item.clustersNum
                    // Object.assign(item, _item)
                  }
                })

                // 活动标签
                this.activityTabs.forEach(_item => {
                  if (_item.id === item.actTagId) {
                    item.productActNew = _item.tagName
                    item.productActTerm = ''
                    item.hasTag = true
                  }
                })
              })
              this.getSkusLive(skus)
              if (this.pageIndex === 1) {
                this.productList = []
              }
              this.productList = this.productList.concat(res.data.productList)
            } else {
              if (this.pageIndex === 1) {
                this.productList = []
              }
            }
            this.newKeyWord = !!this.productList
            console.log(this.newKeyWord)
            // 存放筛选项
            if (this.brandList.length === 0 && this.lstAttributes.length === 0) {
              this.wdMarketFiltrate(this.productCateId || res.data.productCateIds || '')
            }
            this.$util.gioTrackWithVariable('MB13406', {
              // user_id: localStorage.getItem('ucId') ? localStorage.getItem('ucId') + '' : 'null',
              // member_id: localStorage.getItem('mId') ? localStorage.getItem('mId') + '' : 'null',
              search_words: this.keyword ? this.keyword + '' : 'null',
              productId_var: skus.join() || 'null',
              pageNumber: (this.pageIndex + '') || 'null'
            })
            this.searchFlag = true
            this.requestFlag = false
            this.loading = false
            this.isLoading = false
            resolve()
          } else {
            this.productList = []
            this.searchFlag = true
            this.requestFlag = false
            this.loading = false
            this.isLoading = false
            // this.$toast(res.message)
          }
        }).catch(() => {
          this.isSkeletonLoading = false;
        })
      })
    },

    /**
     * 通过skus获取当前是否有直播
     */
    getSkusLive(skus) {
      getSkusLiveInfo(skus).then(res => {
        let result = res.data
        console.log(result, 'result')
        // this.productSkus
        this.productSkus = {
          ...this.productSkus,
          ...result
        }
        console.log(this.productSkus, '===++++ productSkus ++++===')
      })
    },
    /**
     * @description 异步获取信息接口(获取商品价钱)
     */
    getPriceByProductList(traceId) {
      return new Promise((resolve, reject) => {
        getPriceByProductList({
          traceId: traceId,
        }).then(res => {
          if (res.success) {
            resolve(res.products)
          } else {
            this.searchFlag = true
            this.$toast(res.message)
          }
        })
      })
    },
    /**
     * @description 获取商品分类项
     */
    wdMarketFiltrate(productCateId = '') {
      wdMarketFiltrate({
        productCateId: productCateId,
        key: this.keyword
      }).then(res => {
        if (res.success) {
          this.brandList = res.data.brandList || []
          if (this.$route.query.storeType === '5') { // 品旗店隐藏品牌筛选
            this.brandList = [];
          }
          this.lstAttributes = res.data.filterMap ? res.data.filterMap.lstAttributes : []
          // 处理数组，添加默认选中
          this.brandList.forEach((item, index) => {
            item.selected = false
            if (index === 0) {
              item.selected = true
              // 将全部的id改为 all
              item.id = 'all'
            }
          })
          // 删除 null
          let set = new Set(this.lstAttributes)
          set.delete(null)
          this.lstAttributes = Array.from(set)
          this.lstAttributes.forEach((item, index) => {
            item.foldFlag = true // 折叠
            item.lstAttributeOptions.forEach((subItem, subIndex) => {
              subItem.selected = false
              if (subIndex === 0) {
                subItem.selected = true
              }
            })
          })
        } else {
          this.$toast(res.message)
        }
      })
    },
    /**
     * @description 搜索 店铺列表
     */
    searchStore() {
      if (this.requestFlag) {
        return
      }
      this.requestFlag = true
      return new Promise((resolve, reject) => {
        searchStore({
          k: this.keyword || this.placeholder,
          p: this.pageIndex,
          s: this.pageSize,
        }).then(async res => {
          if (res.success) {
            this.finished = res.data ? res.data.length !== this.pageSize : true
            if (this.pageIndex === 1) {
              this.storeList = []
            }
            this.storeList = this.storeList.concat(res.data || [])
            this.searchFlag = true
            this.requestFlag = false
            this.loading = false
            this.isLoading = false
            resolve()
          } else {
            this.searchFlag = true
            this.requestFlag = false
            this.loading = false
            this.isLoading = false
            this.$toast(res.message)
          }
        })
      })
    },
    fnChangeHotModuleShowFlag() {
      this.$util.gioTrackWithVariable('MB15631')
      this.hotModuleShowFlag = !this.hotModuleShowFlag
    },
    /**
     * @description 上拉加载更多
     */
    onLoad() {
      if (this.requestFlag) {
        return
      }
      this.pageIndex++
      if (this.searchType === 'shop') {
        // 搜店铺
        this.searchStore()
        return
      }
      this.wdCommonSearchNew().catch(e => { console.log(e) })
    },
    /**
     * @description 下拉刷新
     */
    onRefresh() {
      // this.productList = []
      this.pageIndex = 1
      // this.finished = false
      if (this.searchType === 'shop') {
        // 搜店铺
        this.searchStore()
        return
      }
      this.wdCommonSearchNew().catch(e => { console.log(e) })
    },
    goBack() {
      this.$store.dispatch('doHistoryBack')
    },
    // fnScrollToTop() {
    //   if (this.$el.querySelector('.target')) {
    //     this.$el.querySelector('.target').scrollIntoView({
    //       block: 'start',
    //       behavior: 'smooth', // 平滑过渡
    //     }) // 回到顶部
    //   }
    // },
    // fnScroll() {
    //   window.addEventListener('scroll', () => {
    //     const _scrollTop = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop
    //     console.log(_scrollTop)
    //     if (_scrollTop > 150) {
    //       this.showToTopFlag = true
    //     } else {
    //       this.showToTopFlag = false
    //     }
    //   })
    // },
    /**
     * @description 初始化参数
     */
    initParam() {
      console.log('====================================');
      console.log('initParam');
      console.log('====================================');
      // this.placeholder = '请输入搜索内容'
      this.searchType = 'goods'
      this.keySearchShowFlag = false
      this.searchFlag = false
      this.hotSearchList = []
      this.keywordsList = []
      this.hotModuleShowFlag = false
      this.keyword = ''
      this.qs = 'isHotDesc'
      this.filterData = 'hasStock@all@0'
      this.pageIndex = 1
      this.pageSize = 10
      this.productList = []
      this.finished = false
      this.loading = false
      this.isLoading = false
      this.filterFlag = false
      this.stock = 'hasStock'
      this.price = ''
      this.brandList = []
      this.lstAttributes = []
      this.showToTopFlag = false
      this.requestFlag = false
      this.storeList = []
    },
    /**
     * @description  我的收获地址列表
     * 返回接口请求数据
    */
    getAddress() {
      const context = this;
      const data = {}
      personalApi.getAddressList(data).then(res => {
        const _resData = res.data;
        if (_resData.success) {
          console.log('addrData')
          context.addrData = _resData.data || [];
          console.log(context.addrData)
        }
      })
    },
  },
}
</script>

